	module.exports = {"data":{"markdownRemark":{"html":"<h2>Amplitude and Period of Sinusoidal Functions from Equation</h2>\n<p>The <strong>generic equation</strong> for a sinusoidal function is:</p>\n<p><img src=\"https://github.com/viso-gio/HostedImages/blob/master/GenericSinusoid.png?raw=true\" alt=\"Generic Sinusoid Equation\"></p>\n<p>Where <em>A</em> represents the <strong>amplitude</strong> of the oscillation, and <em>f</em> the <strong>frequency</strong>.\n<em>Phi</em> is the <strong>phase</strong>, linked to the amount by which the sinusoid is shifted along the <em>x</em> axis.\n<em>Omega</em> is the <strong>angular frequency</strong>, and represents the rate of oscillation as radians per second.</p>\n<p>The <strong>period</strong> of the oscillation, <em>T</em>, is given by:</p>\n<p><img src=\"https://github.com/viso-gio/HostedImages/blob/master/PeriodFormula.png?raw=true\" alt=\"Period\"></p>\n<p>A commonly found <strong>alternative form</strong> of the sinusoid is:</p>\n<p><img src=\"https://github.com/viso-gio/HostedImages/blob/master/AlternativeSinusoid.png?raw=true\" alt=\"Alternative Sinusoid Equation\"></p>\n<p>In this case, the <strong>amplitude</strong> is calculated as:</p>\n<p><img src=\"https://github.com/viso-gio/HostedImages/blob/master/AlternativeAmplitude.png?raw=true\" alt=\"Alternative Amplitude\"></p>\n<h4>More Information:</h4>\n<p>For more informations visit:</p>\n<ul>\n<li><a href='https://www.mathsisfun.com/algebra/amplitude-period-frequency-phase-shift.html' target='_blank' rel='nofollow'>MathIsFun</a></li>\n<li><a href='https://en.wikipedia.org/wiki/Sine_wave' target='_blank' rel='nofollow'>Wikipedia: Sine Wave</a></li>\n</ul>","fields":{"slug":"/mathematics/amplitude-and-period-of-sinusoidal-functions-from-equation/"},"frontmatter":{"title":"Amplitude and Period of Sinusoidal Functions from Equation"}}},"pathContext":{"slug":"/mathematics/amplitude-and-period-of-sinusoidal-functions-from-equation/"}}

